@font-face {
  font-family: "font-1";
  src: url(../../assets/fonts/JosefinSans-ExtraLight.ttf);
}
@font-face {
  font-family: "font-2";
  src: url(../../assets/fonts/JosefinSans-Regular.ttf);
}
@font-face {
  font-family: "font-3";
  src: url(../../assets/fonts/JosefinSans-Bold.ttf);
}

$font-1: "font-1", sans-serif;
$font-2: "font-2", serif;
$font-3: "font-3", serif;

$color-1: rgba(43, 58, 103);
$selected: rgba(23, 190, 187);
$color-3: rgb(246, 239, 166);
$color-4: rgba(42, 61, 69);
$black: hwb(0 16% 84%);
$white: #fbfcf6;
$shadow: rgb(2102, 46, 155);

$Raspberry_Blush: rgba(206, 96, 87);
$Conch_Shell: rgba(230, 188, 172);
$North_Sea_Green: rgb(36, 109, 115);
$New_Age: rgba(215, 206, 209);
$Savannah_Green: rgb(188, 159, 79);

* {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  outline: none;
  border: none;
  text-decoration: none;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;

  &::selection {
    background: $color-1;
  }
}
a,
a:visited {
  color: $black;
}
input,
textarea {
  resize: none;
  border-radius: 20px;
  width: 270px;
  font-family: $font-1;
  background: $white;
}
textarea {
  &:focus {
    box-shadow: 0 0 4px rgba(0, 22, 44, 0.1);
  }
}
button,
input[type="submit"] {
  background: $selected;
  color: $color-1;
  font-family: $font-3;
  cursor: pointer;
  transition: 0.2s;
  margin: 0;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 1.1rem;
  border-radius: 20px 6px 20px 20px;

  &:hover {
    background: $color-1;
    color: $selected;
  }
}
input[type="checkbox"] {
  box-shadow: none;
  width: 20px;
  margin: 10px 0;
}
i {
  transition: 0.15s;

  &:hover {
    transform: scale(1.1);
  }
}
body {
  font-family: $font-2;
  line-height: 1.4rem;
  background: $white;
  color: $black;
  width: 100%;
  height: 100vh;
  background: rgba(233, 236, 231, 0.1);
  background: linear-gradient(90deg, rgba(43, 92, 103, 0.05) 0%, rgba(23, 190, 187, 0.05) 98%);

}
.error {
  color: red;
  font-weight: bold;
}
.success {
  color: green;
  font-weight: bold;
}

.App {
  display: flex;
  flex-direction: column;
  flex-flow: column;
  gap: 1rem;
  width: 100%;
  height: 100vh;
}

#page-wrap {
  width: 100%;
  padding-bottom: 10px;
  padding-top: 10px;
  flex-grow: 1;
  flex-shrink:0;
  flex-basis:auto;

  color: $color-1;
 
  h1{
    font-size: 2rem;
    text-align: center;
    margin: 1rem;
  }

  p{
    font-size: 1.2rem;
    text-align: center;
    margin: 1rem;
  }

}
